.intro {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items:center;
    color: white;    
    z-index: 2;
    transition: all 2s;
    background-color: black;
}

.intro-out{
    display: none;
}

.intro h4{
    position: absolute;
    bottom: 0;
    right: 0; 
    margin: 50px 100px;
}