
.skills{

    position: relative;
    left: auto;
    right:auto;
    padding-left: 100px;
    padding-right: 100px;
    & div{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin: 15px;
    }
    
    & h3,h2 {
        margin: 30px;
    }

}

.skills-image{
    flex-direction: column;
    height: 250px;
    // outline: 2px solid red;
    & img{
        // outline: 2px solid red;
        position: relative;
        bottom: 0px;
        width: 10vw;
        height: 15vh;
        object-fit: contain;
    }
}

@media screen and (max-device-width:850px) {

    .skills{
        padding: 0px !important;

        & div{
            margin: 2vw !important;
        }

        & h3,h2 {
            margin: 0px !important;
        }

        & h1{
            margin-bottom: 10px !important;
        }

    }
   

    .skills-image{
        flex-direction: column;
        height: 150px !important;
        & img{
            object-fit: contain;
            width: 20vw !important;
            height: 10vh !important;
        }
    }

}