body{
    background-color: black;
    font-family: 'Comfortaa', cursive;
    font-family: 'Recursive', sans-serif;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    & h1{
        font-size: 40px;
        margin-bottom: 30px;
    }
    & h2{
        font-size: 30px; 
    }
    & h3{
        font-size: 25px; 
    }
    & h4{
        font-size: 20px; 
    }
    & h5{
        font-size: 15px;
    }
    color: white;
}

.background{
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 100%;
}


.screen{
    min-height: 100vh;
    padding-top: 15vh;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    background-color: rgba(0,0,0, 0.8);
    width: 80vw;
}




.btn{
    font-size: 20px;
    font-family: 'Source Sans Pro', sans-serif;
    border: none;
    padding: 6px 18px;
    cursor: pointer;
    background-color: $light-grey;
    color: #ffffff;

        &:hover{
            transition-duration: .3s;
            background-color: $primary;
        }
    }
 
.portfolio-screen{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media screen and (max-device-width:1200px) {

    body{
        & h1{
            font-size: 32px ;
        }
        & h2{
            font-size: 25px ; 
        }
        & h3{
            font-size: 17px ; 
        }
        & h4{
            font-size: 14px ; 
        }
        & h5{
            font-size: 12px ;
        }
        & p{
            font-size: 80%; 
        }
    }

    .background{
        width:100vw;
    }

}

@media screen and (max-device-width:850px) {

    body{
        width: 100vw;
        & h1{
            font-size: 25px;
        }
        & h2{
            font-size: 20px; 
        }
        & h3{
            font-size: 15px; 
        }
        & h4{
            font-size: 13px; 
        }
        & h5{
            font-size: 10px;
        }
        & p{
            font-size: 65%; 
        }
    }

    .btn{
        padding: 5px !important;
        font-size: 12px;

        &:hover{
            color: #ffffff;
            background-color: $light-grey;
        }
    }

    .screen{
        width: 100%;
        padding-top: 10vh;
    }

}