
.footer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content:space-around;
    outline: 3px solid black;
    background:rgb(0, 0, 0);
    padding: 30px 0px;
}

.icon{
    width: 50px;
    padding: 5px;
    margin: 0 20px 0 20px;
    transition: all .4s ease;

    &:hover{
        transform: scale(1.4);
    }
}

@media screen and (max-device-width:500px) {

    .icon{
        margin: 1vw;
    }

}