.home{
    flex-direction: row;
    justify-content: space-evenly;

    & img{
        width: 20vw;
    }
}

.home-description{
    display: flex;
    flex-direction: column;
    & a{
        margin-top: 10%;
        width: 40%;
    }
    & h3{
        margin-top: 10vh;
    }

    & img{
        position: relative;
        left: 15%;
        margin-top: 15vh;
        width: 15%;
        animation: bounce 1s ;
        animation-iteration-count: infinite;
    }
}

@media screen and (max-device-width:850px) {

    .home{
        flex-direction: column !important;
        align-items: center !important;

        & img{
            width: 150px !important;
        }
    }

    .home-description{

        align-items: center;

        & h3{
            margin-top: 10px !important;
        }

        & img{
            display: none;
        }
    }

}


