.contact{
    padding: 150px;
}

.contact-form{
    display: flex;
    flex-direction: column;
    width: 700px;
    
    & input,textarea{
        color: white;
        border: none;
        background:none;
        border-bottom: 3px solid white;
        font-size: 20px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box; 
    }

    & textarea{
        font-size: 17px;
        resize: none;
        height: 300px;    
        padding: 12px 20px;
    }
}

.contact-item{
    margin: 10px;
    padding: 12px 20px;
    border-radius: 4px;
    outline: none;
}

.btn-send{
    width:90%;
}

@media screen and (max-device-width:1200px) {
    .contact{
        padding: 0px;
    }
    .contact-form{
        width: 600px !important;
    
    }
}

@media screen and (max-device-width:850px) {
    .contact-form{
        width: 90vw !important;

        & input,textarea{
            font-size: 15px;
        }

        & textarea{
            font-size: 13px;
        }
    }
}