.project{
    padding-bottom: 30vh;
}

.project-item {
    position: relative;
    bottom: 300px;

    & .notebook{
        width: 600px;
        position: absolute;
    }

    & .project-img{
        height: 268px;
        width: 480px;
        position: absolute;
        top: 16px;
    }

    & .project-description{
        position: absolute;
        top: -200px;
        margin: 0px 50px;
    }

    & a{
        margin: 20px 20px;
    }

    & p{
        width: 30vw;
    }
}

.project-item-background{
    overflow: hidden;
    height: 600px;
    filter: brightness(0.35);
    
    & .project-background{
        width: 100%;
        height: 900px;
        position: relative;
        bottom: 100px;
        overflow: hidden;
    }   
}

.right{
    & .project-description{
        right: auto;
        left: 0px;
    }

    & .notebook{
        right: 5px;
        left: auto;
    }

    & .project-img{
        right: 65px;
        left: auto;
    }
}

.left{
    & .project-description{
        right: 0px;
        left: auto;
    }
    
    & .notebook{
        right: auto;
        left: 5px;
    }

    & .project-img{
        right: auto;
        left:65px;
    }
}

@media screen and (max-device-width:1200px) {

    .project-item{

        & .project-description{
            top: -230px;
            margin: 0px 30px !important;

            & h1{
                margin: 5px;
            }
        }

        & a{
            margin: 5px;
        }

        & p{
            width: 70vw;
        }

        & .notebook{
            top: 10px;
        }

        & .project-img{
            top: 27px;
        }

    }

}
    
@media screen and (max-device-width:850px) {
    
    .project-item {
        display: flex;
        flex-direction: column;
        align-items: center;
    
        & .notebook{
            width: 300px;
            top:130px;
        }
    
        & .project-img{
            height: 135px;
            width: 240px;
            top: 140px;
        }

       
    }
    
    .project-item-background{
        height: 550px;
        & .project-background{
            width: 100%;
            height: 100%;
        }   
    }

    .right{
        & .project-description{
            right: auto !important;
            left: auto !important;
        }
    
        & .notebook{
            right: auto !important;
            left: auto !important;
        }
    
        & .project-img{
            right: auto !important;
            left: auto !important;
        }
    }
    
    .left{
        & .project-description{
            right: auto !important;
            left: auto !important;
        }
    
        & .notebook{
            right: auto !important;
            left: auto !important;
        }
    
        & .project-img{
            right: auto !important;
            left: auto !important;
        }
    }
}